* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-image: url('./assets/home.jpg');
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  background-color: rgba(147, 49, 49, 0.2);
}

.header_link {
  font-size: 14px;
  text-decoration: none;
  padding: 0 10px;
  color: black;
}

.footer {
  min-height: 50px;
  background-color: rgba(147, 49, 49, 0.2);
}
